import React, {useState} from 'react';

async function handlePersonData(id) {

    const fileName = `../data/${id}.json`;
    const fileImg = `../data/photos/${id}.png`;

    try {
        const response = await fetch(fileName); // 
        if (!response.ok) {
            throw new Error('');
        }
        const data = await response.json();

        const responseImg = await fetch(fileImg); // 
        if (!response.ok) {
            throw new Error('photo');
        }
        const photo = await responseImg;
        data.photo=photo

        return data;

    } catch (error) {
        console.error('JSON', error);
        return "";
    }
}

export default handlePersonData;
